<template>
  <div class="out_wrap">
    <div class="back_wrap">
      <el-button
        @click="$router.go(-1)"
      ><i class="el-icon-arrow-left" />返回</el-button>
      <span class="fs16 bold ml20">{{ $route.query.name || "暂无" }}</span>
    </div>
    <ZfLayout title="基本信息">
      <div class="flex_between mb22">
        <div class="flex1">
          <span class="w150 right inline mr15">企业名称：</span>{{ info.company_info.company_name || '' }}
        </div>
        <div class="flex1">
          <span class="w150 right inline mr15">企业税号：</span>{{ info.company_info.duty || '' }}
        </div>
      </div>
      <div class="flex_between mb22">
        <div class="flex1">
          <span class="w150 right inline mr15">联系人姓名：</span>{{ info.company_info.name || '' }}
        </div>
        <div class="flex1">
          <span class="w150 right inline mr15">联系人电话：</span>{{ info.company_info.mobile || '' }}
        </div>
      </div>
      <div class="flex_between mb22">
        <div class="flex1">
          <span class="w150 right inline mr15">联系人邮箱：</span>{{ info.company_info.email || '' }}
        </div>
        <div class="flex1">
          <span class="w150 right inline mr15">企业法人：</span>{{ info.company_info.legal || '' }}
        </div>
      </div>
      <div class="flex_between mb22">
        <div class="flex1">
          <span class="w150 right inline mr15">企业性质：</span>{{ info.company_info.nature || '' }}
        </div>
        <div class="flex1">
          <span class="w150 right inline mr15">所属行业：</span>{{ info.company_info.industry || '' }}
        </div>
      </div>
      <div class="flex_between mb22">
        <div class="flex1">
          <span class="w150 right inline mr15">企业地址：</span>{{ info.company_info.address || '' }}
        </div>
        <div class="flex1">
          <span class="w150 right inline mr15">企业固话：</span>{{ info.company_info.tel || '' }}
        </div>
      </div>
    </ZfLayout>
    <ZfLayout title="业务合同">
      <div v-for="(item, index) in info.contract_info" :key="index" class="file_item flex">
        <div class="flex1 ellipsis">{{ item.filename || item.file }}</div>
        <div class="w150">{{ item.filesize }}KB</div>
        <div class="w150">
          <a class="add" @click="previewFile(item)">预览</a>
          <a class="add" target="_blank" :href="baseUrl + '/api/downloadFile?file_code=' + item.code">下载</a>
        </div>
      </div>
    </ZfLayout>
  </div>
</template>

<script>
import ZfLayout from '@/components/ZfLayout/index'
import { mapActions } from 'vuex'
export default {
  components: {
    ZfLayout
  },
  data() {
    return {
      baseUrl: process.env.VUE_APP_BASE_API,
      company_id: this.$route.query.company_id,
      info: {
        company_info: '',
        contract_info: []
      }
    }
  },
  mounted() {
    this.detail()
  },
  methods: {
    ...mapActions('accountManage', ['customerDetail']),
    previewFile(item) {
      window.open(`${process.env.VUE_APP_IMAGEVIEW}/onlinePreview?url=${item.fullpath || item.file || item.contract}`)
    },
    async detail() {
      const res = await this.customerDetail({ company_id: this.company_id })
      if (res.code === 200) {
        this.info = res.data
      }
    }
  }
}
</script>

<style></style>
